.feedback {
  --main-text-md-color: var(--gray-70-color);

  padding: 48px 0;
  border-image: fill 0 linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  background-image: url("../img/bg-feedback-packaging.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @include media-min ($lg) {
    padding: 64px 0;
  }

  @include media-min ($xxl) {
    padding: 96px 0;
  }

  &--cosmetics {
    background-image: url("../img/bg-feedback-cosmetics.jpg");
  }

  &__text {
    @include media-min ($lg) {
      max-width: 255px;
    }

    @include media-min ($xxl) {
      max-width: 375px;
    }
  }
}
